<template>
	<view class="container" v-if="loading">
		<!-- <view class="segmented">
			<view :class="{item:true,active:active==index?true:false}" v-for="(item,index) in items" :key="index"
				@click="handleSegmented(index)">
				{{item}}
			</view>
		</view> -->
		<uni-swiper-dot :info="swipers" :current="current" mode="default" :dots-styles="dotsStyles">
			<swiper class="swiper-box" @change="change">
				<swiper-item v-for="(item,index) in swipers" :key="index">
					<image :src="item" mode="scaleToFill" class="thumb">
					</image>
				</swiper-item>
			</swiper>
		</uni-swiper-dot>
		<view class="info">
			<view class="price"><text>{{detail.price}}</text>元/台起</view>
			<view class="sales">已售{{detail.sales}}</view>
		</view>
		<view class="detail-box">
			<view class="detail-box-t">
				{{detail.name}}
			</view>
			<view class="detail-box-c">
				{{detail.description}}
			</view>
		</view>
		<view class="detail-box">
			<view class="detail-box-t">
				收费标准
			</view>
			<view class="detail-box-c">
				因型号、维修难度、品牌不同，最终价格有所差异
			</view>
			<view class="collapse-box">
				<view v-for="(item,index) in shoufei" :key="index">
					<view class="collapse-title" @click="slupDown(index)">
						{{item.title}}
						<view class="more">
							收缩

							<uni-icons type="top" size="12" style="margin-left: 8rpx;"  v-if="collapse[index]"></uni-icons>
							
							<uni-icons type="bottom" size="12" style="margin-left: 8rpx;" v-else></uni-icons>
						</view>
					</view>
					<view v-if="collapse[index]">
						<view class="collapse-lists" v-for="(son,index) in item.value" :key="index">
							<view class="title">{{son.title}}</view>
							<view class="price">{{son.data}}</view>
						</view>
					</view>
				</view>
			</view>
		</view>

		<view class="detail-box" id="detail">
			<view class="detail-box-t center">
				产品详情
			</view>
			<view class="detail-box-c">
				<u-parse :html="detail.content"></u-parse>
			</view>

		</view>
		<uni-goods-nav :fill="true" :options="options" :buttonGroup="buttonGroup" class="navbar" @click="onClick"
			@buttonClick="buttonClick" />
	</view>
</template>

<script>
	import uParse from "@/components/u-parse/u-parse.vue";
	export default {
		data() {
			return {
				loading: false,
				animationData: {},
				id: 0,
				detail: {},
				carts: [],
				swipers: [],
				shoufei: [],
				current: 0,
				active: 0,
				items: [
					'服务', '详情'
				],
				dotsStyles: {
					backgroundColor: 'rgba(47, 128, 255, .3)',
					border: '1px rgba(47, 128, 255, .3) solid',
					color: '#fff',
					selectedBackgroundColor: 'rgba(47, 128, 255, .9)',
					selectedBorder: '1px rgba(47, 128, 255, .9) solid'
				},
				options: [{
					icon: 'home',
					text: '首页'
				}, {
					icon: 'headphones',
					text: '客服',
					infoBackgroundColor: '#007aff'
				}],
				buttonGroup: [{
					text: '立即下单',
					backgroundColor: '#F65425',
					color: '#fff'
				}],
				animation: null,
				collapse: [true, true, true, true, true, true, true, true, true, true]
			}
		},
		onLoad(option) {
			this.id = option.id
			this.getDetail()

		},
		onShow() {
			//this.animation = uni.createAnimation()
		},
		components: {
			uParse
		},
		methods: {
			slupDown(index) {
				this.collapse[index] = this.collapse[index] ? false : true
				this.$forceUpdate()
			},
			change(e) {
				this.current = e.detail.current;
			},
			handleSegmented(index) {
				this.active = index
				var timer = setTimeout(function() {
					var scrollTop;
					var query = uni.createSelectorQuery();
					if (index == 0) {
						scrollTop = 0
					}
					if (index == 1) {
						query.select('#detail').boundingClientRect(res => {
							scrollTop = res.top;
						}).exec()
					}
					uni.pageScrollTo({
						scrollTop: scrollTop,
						duration: 300
					});
					clearTimeout(timer);
				}, 300);
			},

			async getDetail() {
				let res = await this.$u.get('/v2/service/show', {
					id: this.id
				})
				this.detail = res.data[0]
				this.swipers = this.detail.thumbs
				this.shoufei = res.data[1]
				uni.setNavigationBarTitle({
					title: this.detail.name
				})
				this.loading = true
			},
			onClick(e) {
				console.log(e)
				if (e.index == 0) {
					uni.switchTab({
						url: '/pages/index/index'
					})
				}
				if (e.index == 2) {
					uni.switchTab({
						url: '/pages/cart/index'
					})
				}
			},
			buttonClick(e) {
				if (e.index == 0) {
					uni.navigateTo({
						url: '/pages/order/pay?id=' + this.detail.id
					})
				}

			},
			async add_cart() {
				let params = {
					uid: this.vuex_user.id,
					gid: this.detail.id,
					thumb: this.detail.thumb,
					title: this.detail.name,
					price: this.detail.price,
					num: 1
				}
				let res = await this.$u.post('/v2/cart/add', params)
				if (res.data.code == 200) {
					this.$u.toast(res.data.message)
					this.options[2].info = res.data.count
					uni.setTabBarBadge({
						index: 2,
						text: res.data.count
					})
					return
				} else {
					this.$u.toast(res.data.message)
				}
			}

		}
	}
</script>

<style scoped lang="scss">
	.container {
		display: block;
		min-height: 100vh;
		background-color: #F8F8F9;
		position: relative;
	}

	swiper {
		height: 650rpx;
	}

	.segmented {
		background-color: #fff;
		width: 100vw;
		padding: 20rpx 0;
		display: flex;
		justify-content: space-around;
		font-size: 28rpx;
		position: sticky;
		top: var(--window-top);
		z-index: 600;
	}

	.segmented .item.active {
		color: #007AFF;
		border-bottom: #007AFF 1px solid;
		padding-bottom: 10rpx;
	}

	.swiper-box {
		overflow: hidden;
	}

	.swiper-box .thumb {
		width: 100%;
		height: 650rpx;
	}

	.info {
		display: flex;
		justify-content: space-between;
		align-items: center;
		background-color: #fff;
		padding: 20rpx;
	}

	.info .price {
		color: #FF5A5F;
		font-size: 28rpx;
	}

	.info .price text {
		font-size: 48rpx;
	}

	.info .sales {
		color: #999999;
	}

	.detail-box {
		background-color: #fff;
		margin-top: 20rpx;
	}

	.detail-box .detail-box-t {
		font-size: 36rpx;
		font-weight: bold;
		padding: 20rpx;
	}

	.detail-box .detail-box-t.center {
		text-align: center;
	}

	.detail-box .detail-box-c {
		font-size: 24rpx;
		color: #999999;
		padding: 0 20rpx 20rpx 20rpx;
	}

	.navbar {
		position: fixed;
		bottom: 0;
		width: 100vw;
	}


	.collapse-box {
		margin: 0 20rpx;
		padding-bottom: 20rpx;
	}

	.collapse-title {
		padding: 25rpx 20rpx;
		font-size: 28rpx;
		background-color: #F5F5F5;
		margin-top: 20rpx;
		font-weight: bold;
		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	.collapse-title .more {
		color: #A8A8A8;
		font-weight: normal;
		font-size: 24rpx;
	}

	.collapse-lists {
		display: flex;
		justify-content: space-between;
		color: #333;
		height: auto;
		font-size: 24rpx;
		background-color: #FAFAFA;
		margin-top: 1px;
		padding: 25rpx 20rpx;
	}

	.collapse-lists .price {
		color: #999
	}

	.slupDown {}
</style>
